
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import tools from '../../lib/tools.js';

export default {
name:'userIndex',
components:{Group, Cell, CellBox,Actionsheet,Confirm,Datetime,Toast,XDialog,PopupPicker},
directives: {TransferDom},
data(){
return {
id:'',
showSex:false,
showPhone:false,
showBirth:false,
showUserName:false,
showHeight:false,
birth:'',
sex:'',
phone:'',
code:'',
codeText:'发送验证码',
userName:'',
menuSex: {'0':'保密','1':'男','2':'女'},
toastMsg:'',
showToast:false,
showDialog:false,
showDialog2:false,
xDislogText:'',
info:{},
isDisabled:false,
num:59,
contrastMobile:''
}
},
mounted(){
this.id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));// md===userid
if(!this.id || this.id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
this.loading(this.id);
},
methods: {
loading(id){
var vm = this;
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
if(data.data.mobile){
vm.contrastMobile = data.data.mobile;
let temp = data.data.mobile.substr(0,3)+'****'+data.data.mobile.substr(7);
data.data.mobile = temp;
}
vm.info = data.data;
localStorage.setItem('height',vm.info.height || '');
}else{
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
clickUpdate(key){
console.log(key);
if(key=='sex'){
this.showSex = true;
}else if(key=='phone'){
this.showToast = true;
this.toastMsg = '暂不支持修改手机号';
return false;

if(!this.info.parentId || this.info.managerId == this.info.parentId){
this.showToast = true;
this.toastMsg = '主账号无法修改手机号'
return false;
}
this.showDialog2 = true;
}else if(key=='name'){
this.showUserName = true;
}else if(key=='height'){
this.showHeight = true;
}
},
saveSex (key) {

console.log(key)
this.updateInfo('sex',key);
},
showDateTime () {
let vm = this;
this.showBirth = true;
this.$vux.datetime.show({
minYear:1910,
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD',
value: '2017-05-20',
onConfirm (val) {

console.log('plugin confirm', val)
vm.updateInfo('birthday',val);
}
})
},
saveUserName(msg){

console.log(msg);
this.updateInfo('name',msg);
},
updateInfo(type,val){
var vm = this;
if(!this.id){return}
let data = {managerId:vm.id};
data[type] = val;
if(type=='height'){
localStorage.setItem('height',val);
}
if(type=='mobile'){
data.randCode = this.code;
}
this.$http.axios({method:'POST',url:'/apiUser/update',data:data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.showToast = true;
vm.toastMsg = '操作成功'
vm.showDialog2 = false;
vm.loading(vm.id);
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg ='保存失败';
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
toggleAccount(){
tools.next("/m/toggleAccount.html?md="+this.id);// md===userid
},
xDialog(type){
if(type=='upgrade'){
this.xDislogText = '请在电脑登录网址：manager.semioe.com，使用本手机号注册即可升级为专家账号。默认密码:000000'
}else{
this.xDislogText = '客服电话：010-82156628'
}
this.showDialog = true;
},
outLogin(){
localStorage.removeItem('md');
localStorage.removeItem('height');
sessionStorage.removeItem('tempURLtempURL');
localStorage.clear();
sessionStorage.clear();
tools.removeCookie('token');
tools.removeCookie('user_id');
tools.next("/m/login.html");
},
showHeightBox(val){
let reg =  /^[1-9]+(.[0-9]{1})?$/;
if(!reg.test(val)){
this.showToast = true;
this.toastMsg ='请输入真实身高，最多一位小数点哦';
return false;
}
this.updateInfo('height',val);

},
updatePhone(){
if(this.phone){
if(!this.code){
this.showToast = true;
this.toastMsg = '请正确输入手机认证信息!';
return false;
}
}else if(this.code){
if(!this.phone){
this.showToast = true;
this.toastMsg = '请正确输入手机认证信息!';
return false;
}
}
if(this.phone && this.code){
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))){
this.showToast = true;
this.toastMsg = '请正确输入手机号!';
return false;
}
}
this.updateInfo('mobile',this.phone);
},
accountCodeFn(){

if(this.isDisabled){
return ;
}
if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.phone))){
this.showToast = true;
this.toastMsg = '手机号输入错误，请重新输入';
return ;
}

if(this.phone==this.contrastMobile){
this.showToast = true;
this.toastMsg = '输入号码与登录号码相同';
return ;
}

this.isDisabled = true;
let timer = setInterval(() => {
if(this.num==0){
this.codeText = '发送验证码';
this.isDisabled = false;
clearInterval(timer);
return ;
}
this.codeText = this.num+'秒后可重发';
this.num--;
},1000)

this.$http.axios({method:'get',url:'/apiUser/updateRandCode?mobile='+this.phone}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.showToast = true;
this.toastMsg = data.resultCode.message;
}else{
console.log(data.resultCode);
this.showToast = true;
this.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
this.showToast = true;
this.toastMsg = '切换失败';
})
}
}
}



